* {
    box-sizing: border-box;
}

body{
    margin:0;
}
.container {
    max-width: 1060px;
    margin: 0 auto;
    padding-left: 5px;
    padding-right: 50px;
}

ul{
    width: 100%;
}
/* helpers */

.flex {
    display: flex;
}
.flex-row{
    flex-direction: row;
}
.flex-col{
    flex-direction: column;
}
.flex.wrap {
    flex-wrap: wrap;
}
.align-center {
    align-items: center;
}
.text-center{
    text-align: center;
}
.grid{
    display: grid;
}
.space-between {
    justify-content: space-between;
}
.relative{
    position: relative;
}
.first-section{
    background-color: #004047;
    /* height: 100vh; */
    height: 700px;
}
.second-section{
    background-color: #012f34;
        /* height: 100vh; */
        height: 700px;
}
.third-section{
    background-color: #004047;
    height: 745px;
}
.four-section{
    height: 200px;
    background-color: #f67e7e;
}

.footer{
    height: 200px;
background-color: #002529;
}
.delivery-section{

    padding-top: 140px;
}


.delivery-section .details{
grid-template-columns: repeat(3,1fr);
grid-gap: 50px;
}
.info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 100px;
}
.info p{
    text-align: center;
    padding-right: 10px;
color: #ffffff;
font-size: 15px;
font-weight: 600;
line-height: 25px;
z-index: 10;
}
.info .avatar{
    width: 62px;
    height: 62px;
    border-radius: 50%;
    margin-top: 20px;
}
.info .name{
    color: #79c8c7;
font-family: "Livvic - Bold";
font-size: 18px;
font-weight: 700;
line-height: 28px;
}
.info .job{
    color: #ffffff;
font-family: "Livvic - Medium Italic";
font-size: 13px;
font-weight: 500;
line-height: 18px;
}
.delivery-section .main-title{
    color: #ffffff;
font-size: 48px;
font-weight: 700;
line-height: 48px;
}
.delivery-section .green-text{
    color: #79c8c7;
}
.quotes{
    width: 67px;
    height: 56px;
    position: absolute;
    margin-top: -125px;
}
.nav-btn{
    width: 153px;
    height: 48px;
    border-radius: 24px;
    color: white;
    background: transparent;
    border: 2px solid #ffffff;
    font-size: 18px;
    font-weight: 600;
    line-height: 28px;
}
.navbar{
    padding-top: 40px;
}
.nav li{
    list-style: none;
    margin-right: 80px;
    color: #ffffff;
    font-family: "Livvic - Semi Bold";
    font-size: 18px;
    font-weight: 600;
}
.nav li:last-child{
    position: absolute;
    right: 0;
}
.bg-pattern{
    position: absolute;
    left: 0;
}
.bg-pattern-home{
    position: absolute;
    right: 0;
    bottom: 0;
    
}